import * as React from 'react';
import { WeekPlanner } from './weekplanner';
import { TutorableComponent } from '../utility-components/tutorable-component';
import { LanguageContext } from '../utility-components/LanguageContext';
import cfg from '../../globals/KED.cfg';
export function WeekPlannerTutored(props) {
    var intl = props.intl;
    return React.createElement(LanguageContext.Provider, { value: { intl: intl } },
        React.createElement(TutorableComponent, { tutored: true, createComponent: function (env) {
                return React.createElement(WeekPlanner, { key: env.currentUser.mail, env: env, viewCourseUrl: cfg.KED_COURSE_VIEWER_URL });
            } }));
}
