import * as React from 'react';
import { KSTermPlanner } from './termplanner';
import env from '../../globals/KED.env';
import { LanguageContext } from '../utility-components/LanguageContext';
export function KSTermPlannerSelf(props) {
    var intl = props.intl;
    return React.createElement(LanguageContext.Provider, { value: { intl: intl } },
        React.createElement(KSTermPlanner, { env: env, tutored: false, initialDate: new Date() }));
}
