import * as React from 'react';
import { KGTermPlanner } from './kg-termplanner';
import { TutorableComponent } from '../utility-components/tutorable-component';
export function KGTermPlannerTutored() {
    return React.createElement(TutorableComponent, { tutored: true, createComponent: function (env) {
            return React.createElement(KGTermPlanner, { key: env.currentUser.mail, env: env, tutored: true });
        } });
}
