import * as React from 'react';
import { WeekPlanner } from './weekplanner';
import env from '../../globals/KED.env';
import cfg from '../../globals/KED.cfg';
import '../../repos/user-tasks-repo';
import { LanguageContext } from '../utility-components/LanguageContext';
export function WeekPlannerSelf(props) {
    var intl = props.intl;
    return React.createElement(LanguageContext.Provider, { value: { intl: intl } },
        React.createElement(WeekPlanner, { env: env, viewCourseUrl: cfg.KED_COURSE_VIEWER_URL }));
}
