import * as React from 'react';
import { LanguageContext } from '../utility-components/LanguageContext';
import { WeekNotebook } from '.';
export function RootWeekNotebook(props) {
    var intl = props.intl;
    return React.createElement(LanguageContext.Provider, { value: { intl: intl } },
        React.createElement(WeekNotebook, null));
}
